$animation: opacity 1000ms ease-out;
body {
    margin-top: 50px;
    background-image: url('/images/patterns/stardust-black.png');
    background-repeat: repeat;
    background-attachment: fixed;
    .profile-pic {
        background-image: url('/images/profile-pic.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 0 -50px;
        display: block;
        margin: 0 auto;
        height: 200px;
        width: 200px;
        border: 3px solid $scarpa;
        border-radius: 50%;
    }
    h1 {
        color: $scarpa;
        text-align: center;
        font-family: $montserrat;
        font-size: 70px;
        font-weight: lighter;
        margin: 20px;

        @media (max-width:767px) {
            font-size: 50px;
        }
    }
    span.description {
        color: $scarpa;
        font-family: $montserrat;
        display: block;
        text-align: center;
        font-weight: lighter;
        font-size: 17px;
    }

    .links {
        display: block;
        margin: 25px auto 0;
        text-align: center;

        a {
           margin: 0 10px;

            width: 55px;
            height: 60px;
            i {
                font-size: 65px;
                color: $scarpa;
                transition: color 500ms ease;
                &:hover {
                    color: $mint;
                }
            }
        }
    }
    .faders {
        opacity: 0;
        transition: $animation;
    }
    .fade {
        opacity: 1;
    }
}